.First_Form {
  display: inline-block;
  background: white;
  border-radius: 5px;
  width: 100%;
  height: 39px;
  margin-right: 10px;
  margin-bottom: 10px;
  color: #999;
  font-size: 18px; }

.Second_Form {
  display: block;
  background: white;
  border-radius: 5px;
  width: 100%;
  height: 39px;
  margin-bottom: 10px;
  color: #999;
  font-size: 18px; }

.RightSecond select {
  border: 1px solid lightgrey;
  height: 30px;
  font-size: 13px; }

.RightSecond a {
  font-size: 11.5px;
  padding-left: 10px;
  display: inline-block; }

.First_Form,
.Second_Form {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 10px;
  border: 1px solid lightgrey; }
  @media (max-width: 600px) {
    .First_Form,
    .Second_Form {
      width: 100%; } }

.RightSecond {
  text-align: left; }

.RightSecond label {
  margin-right: 20px; }

.FL_Name {
  margin-left: 0px;
  width: 100%; }

.CreateAccount-button {
  text-align: left;
  padding-bottom: 25px;
  border-bottom: 1px solid lightgray; }

.CreateAccount-button input {
  background-color: #69a74e;
  color: white;
  font-size: 20px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  border: 1px solid black;
  padding: 10px 40px; }

.CreateaPage {
  text-align: left;
  font-size: 14px;
  color: #666; }

.response-banner {
  background-color: #666;
  color: white; }



