.list {
  text-align: left;
  min-width: 250px;
  max-width: 250px;
  margin-right: 50px;
  margin-left: 10%;
  margin-top: 50px;
  line-height: 25px;
  border: solid black 1px; }
  @media (max-width: 600px) {
    .list {
      margin: 25px auto; } }

.list-header {
  background-color: #3b5998;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#4e69a2), color-stop(50%, #3b5998));
  background-image: -webkit-linear-gradient(#4e69a2, #3b5998 50%);
  background-image: -o-linear-gradient(#4e69a2, #3b5998 50%);
  background-image: linear-gradient(#4e69a2, #3b5998 50%);
  height: 50px;
  text-align: center;
  line-height: 50px;
  font-size: 20px;
  color: #FDFEFE; }



